:root{
    --white: #ffffff;
    --purple: #230F47;
    --blue: #9AE5E6;
    --black: black;
    --text:'Quicksand', sans-serif;
    
}

html::-webkit-scrollbar{
    width: 0 !important;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.main
{
    align-items: center;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}
.check-btn{
    border: 2px var(--purple);
    font-size: 35px;
    font-family: var(--text);
    color: var(--purple);
    border-radius: 6px;
    box-shadow: 2px 4px 2px;
    box-sizing: border-box;
    background-color: var(--purple);
}
.high
{
    color: var(--white);
    font-family: var(--text);
    font-size: 75px;
    transition: all 0.5s;
}
.high11{
    color: var(--purple);
    font-family: var(--text);
    font-size: 75px;
    transition: all 0.5s;
}
.high3
